@import url(https://fonts.googleapis.com/css2?family=Fraunces:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
:root{
    --primary-orange: orange;
}

.container-fluid .navbar-brand{
    font-size: x-large;
    /* letter-spacing: 2px; */
    font-weight: bolder;
}

.navbar-brand > span{
    color: red;
}

.navbar-nav .nav-link{
    font-weight: bolder;
    font-size: 18px;
}

.navbar-nav > .nav-item{
    margin-right: 0.625rem;
    margin-left: 0.625rem;
}

.navbar-nav > .nav-item:hover, .navbar-nav > .nav-item:focus{
    border-top: solid 0.125rem orange;
    border-top: solid 0.125rem var(--primary-orange);
}

nav{
    z-index: 2;
}

body{
    overflow-x: hidden !important;
}

#header-part{
    position: absolute;
    top: 0;
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center;
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    /* overflow-x: hidden;    */
}

#header-part > .outer-div{
    width: 100vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center !important;
}

.inner-div > p{
    text-align: center;
    color: yellow;
    font-family: 'Fraunces', serif;
    font-size: 70px;
    margin-top: 20px;
}

.styles_typicalWrapper__1_Uvh{
    font-size: 50px;
    font-family: 'Libre Baskerville', serif;
    font-weight: bolder;
    color: white;
}

@media screen and (max-width:1000px){
    .inner-div > p{
        font-size: 37px;
        margin-top: 155px;
    }
    .styles_typicalWrapper__1_Uvh{
        font-size: 30px;
    }
}

